.Container {
	display: flex;
	align-items: center;

	width: 100vw;
	height: 100vh;
	overflow: hidden;

	background: linear-gradient(
		135deg,
		#013e55 0%,
		#013e55 50%,
		#ffffff 50%,
		#ffffff 100%
	);
}

.Form {
	width: 100%;

	padding: 40px;
	padding-top: 20px;
	margin: 15% auto;

	background-color: #ffffff;
	box-shadow: 0px 0px 10px 2px #ccc;

	text-align: center;
}

.Titulo {
	font-size: 24px;
	font-weight: bold;
	color: #013e55;

	margin: 20px 0;
}

.BtnLogin {
	margin-top: 15px;
}

@media (min-width: 700px) {
	.Form {
		width: 480px;
		margin: 5% auto;
	}
}
